let serviceWorkerRegistration = null;

export function registerServiceWorker() {
  if (serviceWorkerRegistration !== null) {
    console.log('Service Worker is already registered.');
    return Promise.resolve(serviceWorkerRegistration);
  }

  if ('serviceWorker' in navigator) {
    return navigator.serviceWorker.register(`${process.env.REACT_APP_PUBLIC_URL}service-worker.js`, { 
        scope: `${process.env.REACT_APP_PUBLIC_URL}` })
      .then((registration) => {
        serviceWorkerRegistration = registration;
        console.log('centralized: Service Worker registered with scope:', registration.scope);
        return registration;
      })
      .catch((err) => {
        console.log('centralized: Service Worker registration failed:', err);
        throw err;
      });
  } else {
    return Promise.reject(new Error('centralized: Service Worker not supported in this browser'));
  }
}