import React, { useEffect, useState } from 'react';
import { isSafariOrIOS } from '../../utils/utils';
import installInstructions from '../../assets/images/pwaInstructions.gif';
import { FaTimes } from 'react-icons/fa';

function InstallPWAButton() {
  const [deferredPrompt, setDeferredPrompt] = useState(null);
  const [showModal, setShowModal] = useState(false);

  useEffect(() => {
    window.addEventListener('beforeinstallprompt', (e) => {
      e.preventDefault();
      setDeferredPrompt(e);
    });
  }, []);

  const handleClick = async () => {
    if (isSafariOrIOS()) {
      setShowModal(true);
    } 
    else if (deferredPrompt) {
      deferredPrompt.prompt();
      const { outcome } = await deferredPrompt.userChoice;
      console.log(`User response to the install prompt: ${outcome}`);
      setDeferredPrompt(null);
    }
  };

  return (
    <>
      <button onClick={handleClick}
        style={{
          backgroundColor: '#FF6F61',
          border: 'none',
          color: 'white',
          padding: '5px 10px',
          textAlign: 'center',
          textDecoration: 'none',
          display: 'inline-block',
          fontSize: '16px',
          margin: '4px 2px',
          cursor: 'pointer',
          borderRadius: '12px'
        }}
      >
        {isSafariOrIOS() ? 'Instructions' : 'Install'}
      </button>
      {showModal && (
        <div className="modal" onClick={() => setShowModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <span className="close-button" onClick={() => setShowModal(false)}><FaTimes /></span>
            <img onClick={() => setShowModal(false)} src={installInstructions} alt="Add to Home Screen Instructions" style={{ maxWidth: '95%', height: 'auto', maxHeight: '100%', borderRadius: '9px' }} />
          </div>
        </div>
      )}
      <style jsx>{`
        .modal {
          display: flex;
          justify-content: center;
          align-items: center;
          position: fixed;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(0, 0, 0, 0.5);
        }
        .modal-content {
          background-color: white;
          padding: 5px;          
          max-width: 100%;
          max-height: 95%;
          display: flex;
          flex-direction: column;
          align-items: center;
          position: relative;
        }
        .close-button {
          position: absolute;
          top: -5px;
          right: -5px;
          width: 30px;
          height: 30px;          
          color: black;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 24px;
          cursor: pointer;
        }
      `}</style>
    </>
  );
}

export default InstallPWAButton;