import React from 'react';
import { Form } from 'react-bootstrap';
import { FaChevronDown } from 'react-icons/fa';

const FormGroupControl = ({ controlId, label, value, onChange, readOnly = false, as = "input", rows = 3, error, options = [] }) => {
  if (as === "checkbox") {
    return (
      <Form.Group controlId={controlId} className="mb-3">
        <Form.Label>{label}</Form.Label>
        <Form.Check
          type="checkbox"
          checked={value}
          onChange={onChange}
          readOnly={readOnly}
          className="ms-2"
        />
        {error && <Form.Text className="text-danger">{error}</Form.Text>}
      </Form.Group>
    );
  }

  if (as === "select") {
    return (
      <Form.Group controlId={controlId} className="mb-3 position-relative">
        <Form.Label>{label}</Form.Label>
        <div className="position-relative flex-grow-1">
          <Form.Control
            as="select"
            value={value}
            onChange={onChange}
            className="w-100"
            readOnly={readOnly}
            isInvalid={!!error}
            style={{ height: '35px', paddingRight: '30px' }}
          >
            {options.map((option, index) => (
              <option key={index} value={option.value}>{option.label}</option>
            ))}
          </Form.Control>
          <FaChevronDown className="position-absolute" style={{ right: '10px', top: '50%', transform: 'translateY(-50%)', pointerEvents: 'none' }} />
        </div>
        <Form.Control.Feedback type="invalid">
          {error}
        </Form.Control.Feedback>
      </Form.Group>
    );
  }

  return (
    <Form.Group controlId={controlId} className="mb-3">
      <Form.Label>{label}</Form.Label>
      <Form.Control
        as={as}
        rows={rows}
        type="text"
        value={value}
        onChange={onChange}
        className="w-100"
        readOnly={readOnly}
        isInvalid={!!error}
      />
      <Form.Control.Feedback type="invalid">
        {error}
      </Form.Control.Feedback>
    </Form.Group>
  );
};

export default FormGroupControl;