import React, { useEffect, useState, useRef } from 'react';

const WebsiteEmbedder = ({ settings }) => {
  const [content, setContent] = useState('');
  const iframeRef = useRef(null);

  useEffect(() => {
    const fetchContent = async () => {
      try {
        if (settings && settings.proxyUrl && settings.websiteUrl) {
          const response = await fetch(`${settings.proxyUrl}?url=${encodeURIComponent(settings.websiteUrl)}`);
          let text = await response.text();

          // Add base tag for resolving relative URLs
          const baseUrl = new URL(settings.websiteUrl);
          text = text.replace(/<head>/i, `<head><base href="${baseUrl.origin}">`);

          // Convert relative URLs to absolute URLs
          text = text.replace(/href="([^"]*)"/g, (match, p1) => `href="${new URL(p1, baseUrl)}"`);
          text = text.replace(/src="([^"]*)"/g, (match, p1) => `src="${new URL(p1, baseUrl)}"`);

          // Remove <meta> refresh tags
          text = text.replace(/<meta[^>]*http-equiv=["']refresh["'][^>]*>/gi, '');

          // Inject script to override redirect functions
          /*
          text = text.replace(/<\/body>/i, `
            <script>
              // Override window.top.location to prevent parent redirection
              Object.defineProperty(window, 'top', {
                get: function() {
                  return window;
                }
              });
              // Override window.location and related functions
              Object.defineProperty(window, 'location', {
                configurable: false,
                enumerable: true,
                writable: false,
                value: window.location
              });
              Object.defineProperty(window.location, 'href', {
                configurable: false,
                enumerable: true,
                writable: false,
                value: window.location.href
              });
              window.location.replace = function() {};
              window.location.assign = function() {};
              window.location.reload = function() {};
            </script>
            </body>
          `);
          */

          /*
          // Inject JavaScript to handle consent banners by button text (case-insensitive)
          text = text.replace(/<\/body>/i, `
            <script>
              document.addEventListener('DOMContentLoaded', function() {
                const buttonTexts = ['continue', 'accept', 'agree', 'consent'];
                const buttons = document.querySelectorAll('button');
                buttons.forEach(button => {
                  if (buttonTexts.includes(button.textContent.trim().toLowerCase())) {
                    button.click();
                  }
                });
              });
            </script>
            </body>
          `);
          */

          // Remove all <script> tags
          //text = text.replace(/<script[^>]*>[\s\S]*?<\/script>/gi, '');

          setContent(text);
        }
      } catch (error) {
        console.error('Failed to fetch content:', error);
      }
    };
    fetchContent();
  }, [settings]);

  useEffect(() => {
    const handleIframeLoad = () => {
      const iframe = iframeRef.current;
      if (iframe) {
        const iframeDocument = iframe.contentDocument || iframe.contentWindow.document;
        iframe.style.height = `${iframeDocument.body.scrollHeight}px`;
      }
    };

    const iframe = iframeRef.current;
    if (iframe) {
      iframe.addEventListener('load', handleIframeLoad);
    }

    return () => {
      if (iframe) {
        iframe.removeEventListener('load', handleIframeLoad);
      }
    };
  }, [content]);

  return (
    <div style={{ width: '100%' }}>
      <iframe
        ref={iframeRef}
        srcDoc={content}
        sandbox="allow-same-origin allow-scripts"
        style={{ width: '100%', border: 'none' }}
        title="Embedded Website"
      />
    </div>
  );
};

export default WebsiteEmbedder;