import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import Header from './components/layout/Header';
import Footer from './components/layout/Footer'; 
import HomePage from './pages/HomePage';
import DefaultRoute from './pages/DefaultRoute';
import { isSafariOrIOS, areNotificationsEnabled, isPWA } from './utils/utils';
import ExternalSiteWindow from './components/common/ExternalSiteWindow';
import { getSetting } from './utils/idb';

function requestNotificationPermission() {
  if ('Notification' in window && navigator.serviceWorker) {
    return Notification.requestPermission().then(permission => {
      if (permission === 'granted') {
        console.log('Notification permission granted.');
      } 
      else {
        console.log('Notification permission denied.');
      }
    }).catch(error => {
      console.error('Error requesting notification permission:', error);
    });
  } 
  else {
    console.error('Notifications or Service Workers are not supported in this browser.');
    return Promise.reject(new Error('Notifications or Service Workers are not supported in this browser.'));
  }
}

function App() {
  const [showPrompt, setShowPrompt] = useState(false);
  const [showWindow, setShowWindow] = useState(false);
  const [externalUrl, setExternalUrl] = useState('');

  const [settings, setSettings] = useState(null);

  useEffect(() => {
    if (isSafariOrIOS() && !areNotificationsEnabled()) {
      if(isPWA()){
        setShowPrompt(true);      
      }
    }
    else{
      setShowPrompt(false); 
      requestNotificationPermission();              
    }

    const loadSettings = async () => {
      const currentSettingName = await getSetting('current-setting');
      if (currentSettingName) {
        const currentSetting = await getSetting(currentSettingName);
        if (currentSetting) {
          setSettings(currentSetting);
        }
      }
    };
    loadSettings();

    const observer = new MutationObserver((mutationsList, observer) => {
      for (const mutation of mutationsList) {
        if (mutation.type === 'childList') {
          const frameContainer = document.getElementById('be-frame');
          if (frameContainer) {
            const iframe = frameContainer.querySelector('iframe');

            if (iframe) {
              iframe.addEventListener('load', () => {
                const iframeDocument = iframe.contentDocument || iframe.contentWindow.document;
                const handleLinkClick = (event) => {
                  const target = event.target;
                  if (target.tagName === 'A') {
                    event.preventDefault();
                    handleOpenWindow(target.href.replace(/&#61;/g, '='));
                  }
                };
                iframeDocument.addEventListener('click', handleLinkClick);

                return () => {
                  iframeDocument.removeEventListener('click', handleLinkClick);
                };
              });
              
            }          
            if(iframe){
              observer.disconnect(); // Stop observing
            }
          }
        }
      }
    });

    observer.observe(document.body, { childList: true, subtree: true });

    return () => {
      observer.disconnect();
    }; 

  },[]);

  const handleOpenWindow = (url) => {
    setExternalUrl(url);
    setShowWindow(true);
  };

  const handleCloseWindow = () => {
    setShowWindow(false);
    setExternalUrl('');
  };

  const handleEnableNotifications = () => {
    requestNotificationPermission().then(() => {
      setShowPrompt(false);
    }).catch(error => {
      console.error('Failed to enable notifications:', error);
    });
  };

  return (
    <Router basename={process.env.REACT_APP_PUBLIC_URL}>
      <Header /> 
      <Routes>
        <Route path="/" element={<HomePage settings={settings} />} />
        <Route path="*" element={<DefaultRoute />} />
      </Routes>
      <Footer setSettings={setSettings} />
      {showPrompt && (
        <div className="notification-overlay d-flex justify-content-center align-items-center">
          <div className="card p-4 text-center">
            <p>
              In order for the site to work correctly, you need to enable notifications. To enable notifications, click the button below.
            </p>
            <button className="btn btn-primary" onClick={handleEnableNotifications}>
              Enable Notifications
            </button>
          </div>
        </div>
      )}

      {showWindow && <ExternalSiteWindow url={externalUrl} onUnload={handleCloseWindow} />}
    </Router>
  );
}

export default App;