import { openDB } from 'idb';

const dbPromise = openDB(process.env.REACT_APP_IDB_NAME, 1, {
  upgrade(db) {
    db.createObjectStore('settings');
  },
});

export const getAllSettings = async () => {
  const db = await dbPromise;
  return db.getAllKeys('settings');
};

export const getSetting = async (key) => {
  const db = await dbPromise;
  return db.get('settings', key);
};

export const saveSetting = async (key, value) => {
  const db = await dbPromise;
  await db.put('settings', value, key);
};

export const deleteSetting = async (key) => {
  const db = await dbPromise;
  await db.delete('settings', key);
};

export const exportSettings = async () => {
    const db = await dbPromise;
    const tx = db.transaction('settings', 'readonly');
    const store = tx.objectStore('settings');
    const allKeys = await store.getAllKeys();
    const allValues = await store.getAll();
    const allData = allKeys.map((key, index) => ({ key, value: allValues[index] }));
    return allData;
};
  
  
  
export const importSettings = async (data) => {
    const db = await dbPromise;
    const tx = db.transaction('settings', 'readwrite');
    const store = tx.objectStore('settings');
    for (const { key, value } of data) {
      await store.put(value, key);
    }
    await tx.done;
  };
  