import React, { useState, useEffect } from 'react';
import { Container, Navbar, Button } from 'react-bootstrap';
import { FaCog } from 'react-icons/fa';
import { isPWA, isInstalledPWA } from '../../utils/utils';
import InstallPWAButton from '../common/InstallPWAButton';
import SettingsForm from '../common/SettingsForm';
import logo from '../../assets/images/logo192.png';
import '../../assets/styles/footer.css';

function Footer({ setSettings }) {
  const [showInstallPWA, setShowInstallPWA] = useState(false);
  const [showSettings, setShowSettings] = useState(false);

  useEffect(() => {    
    if (!isPWA()) {
      console.log('Checking PWA status...');
      isInstalledPWA(isInstalled => {
          console.log('PWA installed:', isInstalled);
          setShowInstallPWA(!isInstalled);
      });
    }
  }, []);

  const handleShowSettings = () => setShowSettings(true);
  const handleCloseSettings = () => setShowSettings(false);
  const handleSaveSettings = (newSettings) => setSettings(newSettings);

  return (
    <>
      <Navbar bg="dark" variant="dark" className="footer">
        <Container className="d-flex justify-content-center">
          {showInstallPWA && <InstallPWAButton />}    
          <div className="d-flex justify-content-center align-items-center">
            <Button variant="link" onClick={handleShowSettings}>
              <FaCog size={24} color="white" />
            </Button>      
            <Navbar.Text className="text-center">
              <img src={logo} alt="Logo" style={{ height: '24px', verticalAlign: 'middle', marginRight: '8px', borderRadius: '50%' }} />
              &copy; {new Date().getFullYear()} 
              &nbsp;{process.env.REACT_APP_COMPANY_NAME}
            </Navbar.Text>
          </div>                    
        </Container>
      </Navbar>
      <SettingsForm
        show={showSettings}
        handleClose={handleCloseSettings}
        saveSettings={handleSaveSettings}
      />
    </>
  );
}

export default Footer;