import { registerServiceWorker } from './centralizedServiceWorker';
import { getSetting } from './utils/idb';

let serviceWorkerRegistration = null;

export function register() {
  window.addEventListener('load', function() {
    registerServiceWorker()
      .then((registration) => {
        serviceWorkerRegistration = registration;
        console.log('serviceWorkerRegistration.js - Service Worker registered with scope:', registration.scope);
        AddGuideScript(); 
      })
      .catch((err) => {
        console.log('Service Worker registration failed:', err);
      });
  });
}

export function unregister() {
  if (serviceWorkerRegistration) {
    serviceWorkerRegistration.unregister();
  }
}

function SendNotification(settings){
  let companyName = '';
  if(settings && settings.companyName){
    companyName = settings.companyName;
  }
  if ('serviceWorker' in navigator) {
    navigator.serviceWorker.ready.then(registration => {
      registration.active.postMessage({ 
        type: 'CHAT_MESSAGE_POSTED', 
        companyName: companyName 
      });
    });
  }
}

function SetOnFocus(){
  const textArea = document.querySelector('form[data-selector="REPLY_BOX"] textarea[name="reply-box"]');
  textArea.addEventListener('focus', () => {
    document.body.style.paddingBottom = '300px'; // Adjust based on keyboard height
  });
  textArea.addEventListener('blur', () => {
    document.body.style.paddingBottom = '0';
  });
}

/* eslint-disable no-undef */
function AddGuideScript() { 
  window.CXoneDfo = 'cxone';
  window.cxone = window.cxone || function() {
    (window.cxone.q = window.cxone.q || []).push(arguments);
  };
  window.cxone.u = 'https://web-modules-de-na1.niceincontact.com/loader/1/loader.js';
  const script = document.createElement('script');
  script.type = 'module';
  script.src = window.cxone.u + '?' + Math.round(Date.now() / 1e3 / 3600);
  const publicUrl = process.env.REACT_APP_PUBLIC_URL;

  let settings = {};
  const loadSettings = async () => {
    const currentSettingName = await getSetting('current-setting');
    if (currentSettingName) {
      settings = await getSetting(currentSettingName);
    }
  };  
  loadSettings();

  script.onload = () => {
    try {
      // Wait until cxone is defined
      const checkCxone = setInterval(() => {
        console.log('cxone not defined yet');
        if (typeof window.cxone !== 'undefined') {
          clearInterval(checkCxone);
          console.log('cxone defined'); 
          window.cxone('init', '1092');         
          if (settings) {
            if (settings.guideInitId) {              
              window.cxone('guide', 'init', settings.guideInitId);
            }
            if (settings.skillId) {
              window.cxone('chat', 'setCaseCustomField', 'dse_skill', settings.skillId);
            }
            if (settings.customIvaJson) {
              window.cxone('chat', 'setCaseCustomField', 'dse_iva_params', settings.customIvaJson);
            }
            if (settings.companyName) {
              window.cxone('chat', 'setCaseCustomField', 'companyname', settings.companyName);
            }
            if (settings.botId) {
              window.cxone('chat','setCaseCustomField', 'bot', settings.botId);
            }
            if (settings.guideStyles) {
              window.cxone('guide', 'setCustomCss', settings.guideStyles);
            }
            if (settings.chatStyles) {
              window.cxone('chat', 'setCustomCss', settings.chatStyles);
            }
            if (settings.hidePreSurvey) {
              window.cxone('guide', 'hidePreSurvey');
            }
          }          
          window.cxone('chat', 'onPushUpdate', ['MessageAddedIntoCase'], () => SendNotification(settings)); 
          window.cxone('chat','showSendButton');
          //window.cxone('chat','showCustomerAvatar');                   
        }
      }, 100); // Check every 100ms
    } 
    catch (error) {
      console.error('Error executing cxone functions:', error);
    }
  };
  document.body.appendChild(script);
}
/* eslint-enable no-undef */