import React from 'react';
import NewWindow from 'react-new-window';

const ExternalSiteWindow = ({ url, onUnload }) => {
  const domain = new URL(url).hostname;

  return (
    <NewWindow url={url} onUnload={onUnload} features={{ width: 800, height: 600 }}>
      <div className="external-site-header bg-dark text-white d-flex justify-content-center align-items-center" style={{ padding: '5px', height: '50px', borderBottom: '1px solid #ccc', position: 'relative', zIndex: 10 }}>
        <h2 style={{ margin: 0, fontSize: '16px', flexGrow: 1, textAlign: 'center' }}>{domain}</h2>
        <button className="close-button btn btn-danger" style={{ position: 'absolute', right: '10px' }} onClick={onUnload}>Close</button>
      </div>
      <iframe src={url} className="external-site-iframe" style={{ width: '100%', height: 'calc(100% - 50px)', border: 'none', position: 'relative', zIndex: 1 }}></iframe>
    </NewWindow>
  );
};

export default ExternalSiteWindow;
